h1,
h2,
h3,
h4,
h5,
h6,
ul {
  margin: 0;
  padding: 0;
}
body {
  background-color: #fff;
  font-family: $osr;
  color: #000;
  font-size: 14px;
  background: linear-gradient(to right);
}
