.title {
  font-size: 56px;
  line-height: 56px;
  font-family: $pdr;
  margin-bottom: 16px;
  @include lg(font-size, 46px);
  @include md(font-size, 36px);
  @include lg(line-height, 46px);
  @include md(line-height, 38px);
}
@include reset();
.d-flex {
  display: flex;
}
a {
  text-decoration: none;
}
.text {
  line-height: 40px;
  color: #747474;
  @include md(line-height, 28px);
  
}
.page {
  max-width: 1920px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.footer {
  margin-top: auto;
}

.your-favorite-image {
  object-fit: cover;
  font-family: "object-fit: cover";
}
img {
  
  // max-width: none;
}
::-ms-backdrop, img { max-width: none; } /* IE11 */
.order-now {
  text-transform: uppercase;
  font-size: 14px;
  font-family: $ose;
  color: $white;
  background-color: $red;
  padding: 16px 32px;
  text-align: center;
  line-height: 19px;
  letter-spacing: 1px;
  &:hover {
    color: $black;
    background-color: $white;
  }
  &_color {
    background-color: $white;
    color: $black;
    &:hover {
      color: $black;
      background-color: $silverBlack;
    }
  }
  &_contact {
    margin-top: 40px;
    display: inline-block;
  }
}
.container {
  @include container();
  @include sm(max-width, rem(510));
  @include md(max-width, rem(768));
  @include lg(max-width, rem(992));
  @include xl(max-width, rem(1170));
  @include xlg(max-width, rem(1300));
}
