.header {
    position: relative;
    margin-bottom: -4px;
    &__wrapper {
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }
    &__image {
        img {
            @include md(height, 500px);
        }
    }
    &__top {
        margin-left: 175px;
        margin-right: 175px;
        @include xl(margin-left, 75px);
        @include xl(margin-right, 75px);
        @include md(margin-left, 0);
        @include md(margin-right, 0);
        margin-top: 37px;
        display: flex;
        justify-content: space-between;
        @include md(flex-direction, column);
        @include md(text-align, center);
    }
    &__logo {
        @include md(margin-bottom, 30px);
    }
    &__btn {
        display: flex;
        @include md(justify-content, center);
    }
    &__des {
        color: #fff;
        margin-left: 19%;
        margin-top: 18%;
        @include xlg(margin-left, 14%);
        @include xlg(margin-top, 12%);
        @include lg(margin, 0 auto);
        @include lg(margin-top, 40px);
        @include lg(width, 100%);
        @include lg(text-align, center);
        &-logo {
            margin-bottom: 5px;
        }
        &-text {
            font-size: 16px;
            line-height: 26px;
            color: $silverBlack;
            width: 550px;
            @include lg(width, 100%);
            @include lg(padding-left, 15px);
            @include lg(padding-right, 15px);
            @include lg(display, table-cell);
        }
        &-link {
            text-transform: uppercase;
            color: $red;
            font-family: $ose;
            line-height: 26px;
            letter-spacing: 1px;
            padding-left: 100px;
            position: relative;
            margin-top: 60px;
            display: inline-block;
            @include xlg(margin-top, 30px);
            @include lg(margin-top, 15px);

            &:before {
                content: "";
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(50%);
                width: 80px;
                height: 1px;
                background-color: $red;
            }
            &:hover {
                color: $white;
                &:before {
                    background-color: $white;
                }
            }
        }
    }
}

.custom-select select {
    border-radius: 0;
    background-color: rgba(255, 255, 255, 0.4);
    padding: 16px;
    border: 0;
    font-size: 14px;
    line-height: 19px;
    font-family: $ose;
    color: $white;
    outline: none;
    text-transform: uppercase;
    cursor: pointer;
    padding-right: 5px;
}
.custom-select select option {
    color: #000;
    text-transform: uppercase;
}
