.footer {
    background-color: #fff;
    padding: 56px 0;
    padding-left: 375px;
    @include lg(padding, 36px 15px);
    @include lg(text-align, center);
    &__social {
        margin-bottom: 55px;
        ul {
            display: flex;
            @include lg(justify-content, center);
            li {
                margin-right: 90px;
                @include lg(margin-right, 40px)
                a {
                    color: $silverBlack;
                }
            }
        }
    }
    &__copyPaste {
        font-size: 16px;
        line-height: 26px;
        color: $silverBlack;
    }
}