.content {
    background-color: $white;
    padding-top: 110px;
    @include lg(padding-top, 30px);
    &__marca {
        display: flex;
        @include container-full();
        justify-content: center;
        margin-bottom: 37px;
        @include md(flex-direction, column-reverse);

        &-img {
            position: relative;
            max-width: 470px;
            min-height: 625px;
            padding-bottom: 160px;
            margin-right: 130px;
            @include xlg(margin-right, 50px);
            @include xl(max-width, 320px);
            @include xl(min-height, 450px);
            @include md(margin-right, 0px);
            @include lg(padding-bottom, 30px);
            @include md(min-height, none);
            @include md(max-width, none);
            @include md(width, 100%);
            @include md(height, 450px);
            &:before {
                content: "";
                position: absolute;
                top: 140px;
                left: -90px;
                background: url(../img/obj.png) no-repeat;
                width: 100%;
                height: 100%;
                @include xl(width, 170px);
                @include xl(top, 40px);
                @include xl(left, -70px);
                @include lg(display, none);
            }
            img {
                width: 100%;
                height: 100%;
                position: relative;
                z-index: 1;
            }
        }
    }
    &__interior {
        display: flex;
        justify-content: flex-end;
        position: relative;
        // padding-bottom: 435px;
        @include lg(flex-direction, column);
        @include lg(text-align, center);
        // @include lg(padding-bottom, 0);
        @include lg(padding-right, 15px);
        @include lg(padding-left, 15px);
        // &::after {
        //     content: "";
        //     position: absolute;
        //     bottom: 0;
        //     width: 100%;
        //     height: 435px;
        //     // background-color: $black;
        //     @include lg(display, none);
        // }
        &-img {
            max-width: 850px;
            min-height: 863px;
            @include xlg(max-width, 650px);
            @include xlg(min-height, 763px);
            @include lg(min-height, none);
            @include lg(max-width, none);
            @include lg(width, 100%);
            @include lg(height, 400px);
            z-index: 1;

            img {
                width: 100%;
                height: 100%;
            }
        }
        &-wrap {
            position: relative;
            padding-left: 15px;
            @include lg(padding-left, 0);
        }
    }
    &__powertrain {
        display: flex;
        @include lg(flex-direction, column-reverse);
        @include lg(padding-left, 15px);
        @include lg(padding-right, 15px);
        &-img {
            background-image: url(../img/Изображение11.jpg);
            background-repeat: no-repeat;
            background-size: auto;
            background-position: -420px 0;
            padding: 170px 277px;
            position: relative;
            @include xlg(padding, 125px 177px);
            @include xl(padding, 70px 55px);
            @include lg(padding, 70px 0);
            @include lg(text-align, center);
            &:before {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                // background-color: rgba(255, 255, 255, 0.315);
                background: rgb(255, 255, 255);
                background: linear-gradient(
                    90deg,
                    rgba(255, 255, 255, 0.33657212885154064) 25%,
                    rgba(255, 255, 255, 1) 93%,
                    rgba(255, 255, 255, 1) 100%
                );
                width: 100%;
                height: 100%;
            }

            img {
                position: relative;
                z-index: 2;
            }
        }
        &-inner {
            &:after {
                content: "";
                position: absolute;
                top: 376px;
                left: -124px;
                background: url(../img/obj2.png) no-repeat;
                width: 100%;
                height: 100%;
                background-size: contain;
                @include lg(display, none);
            }
        }
    }
    &__contact {
        display: flex;
        justify-content: space-around;
        background: url(../img/Изображение13.jpg);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: 132% 0;
        padding: 150px;
        position: relative;
        @include xlg(background-position, 200%);
        @include lg(background-position, 320px);
        @include xl(padding, 100px);
        @include lg(flex-direction, column);
        @include lg(text-align, center);
        @include lg(padding, 30px 15px);
        @include lg(background-size, auto);

        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            background: rgb(245, 245, 240);
            background: linear-gradient(
                90deg,
                rgba(245, 245, 240, 1) 0%,
                rgba(245, 245, 245, 1) 71%,
                rgba(255, 255, 255, 0.33657212885154064) 100%
            );
            width: 100%;
            height: 100%;
            @include sm(background, #fff);
        }
        &-inner {
            position: relative;
            width: 670px;
            max-width: 670px;
            @include xlg(width, auto);
            @include xlg(max-width, fit-content);
            @include lg(margin, 0 auto);
        }
        &-wrap {
            z-index: 1;
            background-color: #151515;
            padding: 100px;
            color: $white;
            position: absolute;
            @include xlg(position, static);
            @include xlg(padding, 50px);
            @include sm(padding, 30px);
        }
        &-group {
            display: flex;
            flex-direction: column;
            &:first-child {
                margin-bottom: 80px;
                @include xlg(margin-bottom, 50px);
            }
        }
        &-title {
            text-transform: uppercase;
            font-family: $ose;
            line-height: 26px;
            letter-spacing: 1px;
            position: relative;
            padding-bottom: 26px;
            margin-bottom: 45px;
            @include xl(margin-bottom, 35px);
            @include xl(padding-bottom, 15px);
            @include lg(letter-spacing, 0);

            &:after {
                content: "";
                position: absolute;
                bottom: 0;
                left: 0;
                width: 80px;
                height: 1px;
                background-color: #fff;
                @include lg(left, 50%);
                @include lg(transform, translate(-50%, -50%));
                
            }
        }
        &-call,
        &-email {
            font-family: $ose;
            font-size: 32px;
            line-height: 59px;
            background-color: $red;
            padding: 20px 40px;
            position: relative;
            padding-left: 120px;
            @include xlg(line-height, 32px);
            @include xl(padding, 15px 20px);
            @include xl(font-size, 20px);
            @include xl(padding-left, 100px);
            @include sm(padding, 15px 10px);
            @include sm(font-size, 15px);
            @include sm(padding-left, 70px);

            &:before {
                font-family: "FontAwesome";
                font-weight: 400;
                position: absolute;
                top: 0;
                left: 0;
                width: 80px;
                height: 100%;
                background-color: #fff;
                color: #000;
                display: flex;
                justify-content: center;
                align-items: center ;
                @include xl(width, 60px);

            }
        }
        &-call {
            &:before {
                content: "\f879";
            }
        }
        &-email {
            &:before {
                content: "\f0e0";
            }
        }
    }
    &__item {
        width: 570px;
        margin-top: 110px;
        @include xl(margin-top, 0);
        @include md(width, 100%);
        @include md(text-align, center);
        z-index: 1;
        &-hedader {
            line-height: 26px;
            letter-spacing: 1px;
            color: $red;
            position: relative;
            padding-bottom: 25px;
            display: block;
            margin-bottom: 15px;
            text-transform: uppercase;
            &:after {
                content: "";
                position: absolute;
                bottom: 0;
                left: 0;
                width: 80px;
                height: 1px;
                background-color: $red;
                @include md(left, 50%);
                @include md(transform, translate(-50%, -50%));
            }
            &_center {
                &:after {
                    @include lg(left, 50%);
                    @include lg(transform, translate(-50%, -50%));
                }
            }
        }

        &-btn {
            background-color: $red;
            padding: 74px 48px;
            position: absolute;
            bottom: 0;
            width: 100%;
            @include lg(position, relative);
            @include lg(padding, 25px 0);
        }
        &_marca {
            @include md(margin-bottom, 30px);
        }
        &_interior {
            margin-right: 130px;
            @include xl(margin-right, 40px);
            @include xlg(width, 450px);
            @include lg(width, 100%);
            @include lg(margin-bottom, 30px);
        }
        &_powertrain {
            @include xl(width, 500px);
            @include lg(width, 100%);
            @include lg(text-align, center);
            @include lg(margin-top, 30px);
            @include lg(margin-bottom, 30px);
            @include xl(margin-top, 50px);
        }
        &-des {
            display: flex;
            flex-direction: column;
            margin-top: 70px;
            text-align: left;
            @include lg(margin-top, 20px);
            span {
                font-size: 18px;
                line-height: 40px;
                color: #747474;
                position: relative;
                padding-left: 62px;
                @include sm(font-size, 14px);
                @include sm(padding-left, 0);

                &:before {
                    content: "";
                    position: absolute;
                    width: 36px;
                    height: 3px;
                    background-color: $red;
                    top: 50%;
                    left: 0;
                    @include sm(display, none);
                }
            }
            b {
                color: $red;
                font-family: $ose;
            }
        }
        &-logo {
            margin-bottom: 20px;
        }
        &-contact {
            margin-right: 20px;
            @include lg(margin, 0px);
            @include lg(width, 100%);
            @include lg(margin-bottom,20px);
        }
    }
}

::-ms-backdrop,
.content__interior {
    padding-bottom: 0;
} /* IE11 */
::-ms-backdrop,
.content {
    height: 100%;
} /* IE11 */
